.temp-container {
  display: flex; }

.temp-number {
  color: #000;
  font-size: 48px; }

.temp-scale {
  display: flex;
  font-size: 14px;
  margin-left: 4px; }
  .temp-scale .temp-divider {
    margin: 0 4px; }
  .temp-scale a {
    text-decoration: none; }
    .temp-scale a:hover, .temp-scale a:focus {
      outline: 1px solid;
      outline-offset: 2px; }
