.weather-widget {
  border: none;
  border-radius: 12px;
  box-shadow: inset 0 0 0 3px #e4dfdf, 0 3px 5px -1px rgba(0, 0, 0, 0.1), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 6px 10px 0 rgba(0, 0, 0, 0.14);
  margin: auto;
  max-width: 800px;
  padding: 24px; }
  .weather-widget .weather-widget-weather-conditions > div {
    display: inline-block;
    width: 50%; }
  @media (max-width: 768px) {
    .weather-widget .weather-widget-weather-conditions .weather-widget-conditions {
      text-align: right; }
      .weather-widget .weather-widget-weather-conditions .weather-widget-conditions p {
        font-size: 1em; } }
  .weather-widget .weather-widget-current-weather {
    display: flex;
    justify-items: center;
    margin-top: 24px; }
    .weather-widget .weather-widget-current-weather .weather-widget-icon {
      margin-right: 8px; }
  .weather-widget .weather-widget-daily-weather {
    display: flex;
    justify-content: space-evenly;
    margin-top: 32px;
    overflow: auto; }
    @media (max-width: 768px) {
      .weather-widget .weather-widget-daily-weather {
        justify-content: flex-start; }
        .weather-widget .weather-widget-daily-weather .daily-weather-container {
          margin-right: 8px; } }
